@font-face {
  font-family: 'PT Mono';
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"), url("../fonts/PTMono-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Roboto Condensed';
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"), url("../fonts/RobotoCondensed-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal; }

.page {
  min-width: 320px; }

.page__wrapper {
  background-color: #1B2D37;
  background-image: url("../images/background-header.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

.header {
  margin: 0 auto;
  padding: 31px 50px;
  box-sizing: border-box;
  width: 100%;
  max-width: 1510px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

@media (max-width: 1023px) {
  .header {
    padding: 20px 25px; } }

@media (max-width: 767px) {
  .header {
    padding: 17px; } }

.header__link-logo {
  margin: 0;
  padding: 0;
  text-decoration: none; }

@media (max-width: 767px) {
  .header__link-logo {
    width: 38px;
    height: 38px;
    overflow: hidden; } }

.header__logo {
  margin: 0;
  padding: 0;
  width: 202px;
  height: 52px; }

@media (max-width: 767px) {
  .header__logo {
    width: 147px;
    height: 38px; } }

.header__wrapper {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  margin-left: 50px; }

.header__contacts {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.header__item {
  margin: 0;
  padding: 0;
  margin-right: 88px; }
  .header__item:last-child {
    margin-right: 0; }

@media (max-width: 1023px) {
  .header__item {
    margin-right: 33px; } }

@media (max-width: 767px) {
  .header__item {
    margin-right: 22px; } }

.header__contact {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  text-decoration: none;
  max-width: 250px;
  font-family: 'PT Mono';
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #FFFFFF; }
  .header__contact:hover, .header__contact:focus {
    color: #00B2FF; }

.header__contact-icon {
  margin: 0;
  padding: 0; }

.header__contact-icon_type_email {
  width: 19px;
  height: 14px;
  margin-right: 20px; }

@media (max-width: 1023px) {
  .header__contact-icon_type_email {
    width: 25px;
    height: 19px;
    margin-right: 0; } }

.header__contact-icon_type_phone {
  width: 18px;
  height: 18px;
  margin-right: 16px; }

@media (max-width: 1023px) {
  .header__contact-icon_type_phone {
    width: 24px;
    height: 24px;
    margin-right: 0; } }

.header__contact-title {
  margin: 0;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }

@media (max-width: 1023px) {
  .header__contact-title {
    display: none; } }

.header__button {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 180px;
  height: 45px;
  margin: 0;
  padding: 0;
  margin-left: 50px;
  font-family: 'Roboto Condensed', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  background-color: inherit;
  cursor: pointer; }
  .header__button:hover, .header__button:focus {
    color: #00B2FF;
    border: 1px solid #00B2FF; }

@media (max-width: 1023px) {
  .header__button {
    display: none; } }
